.gigCards{
    // width: 234px;
    // height: 370px;
    width: 300px;
    height: 420px;
    border: 1px solid rgb(230, 227, 227);
    margin-bottom: 25px;

    img{
        width: 100%;
        height: 35%;
        object-fit: cover;
    }
    .info{
        padding: 10px 20px;
        display: flex;
        flex-direction: column;
        gap: 15px;

        .user{
            display: flex;
            align-items: center;
            gap: 10px;

            img{
                width: 26px;
                height: 26px;
                border-radius: 50%;
            }
        }
        p{
            color: #111;
        }
        .star{
            display: flex;
            align-items: center;
            gap: 5px;

            img{
                width: 14px;
                height: 14px;
            }

            span{
                font-size: 14px;
                font-weight: bold;
                color: #ffc108;
            }
        }
    }
    hr{
        height: 0;
        border: 0.5px solid lightgray;
    }
    .details{
        padding: 10px 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        img{
        width: 16px;
        height: 16px;
        cursor: pointer;
        }

        .price{
            span{
                color:#999;
                font-size: 12px;
            }
            h2{
                color:#555;
                font-size: 18px;
                font-weight: 400;  
            }
        }
        
    }

}