.gigs{
    display: flex;
    justify-content: center;
    .container{
        width: 1400px;
    padding: 30px 0px;
    display: flex;
    flex-direction: column;
    gap: 15px;

    .breadcrums{
        font-weight: 300;
        font-size: 13px;
        color: #555;
    }
        p{
            color: #999;
            font-weight: 300;
        }

        .menu{
            display: flex;
            align-items: center;
            justify-content: space-between;

            .left{
                display: flex;
            align-items: center;
            gap: 10px;
            color: #555;
            font-weight: 300;

            input{
                padding: 5px;
                border: 1px solid lightgray;
                 outline: none;
                 
                 &::placeholder{
                    color: gray;
                 }
            }
            button{
                color: white;
                font-weight: 500;
                border: none;
                background-color:  #1dbf73;
                cursor: pointer;
                padding: 5px 10px;
                border-radius:5px;
            }
        }
            .right{
                display: flex;
                align-items: center;
                gap: 10px;
                position: relative;

                .sortBy{
                    font-weight: 300;
                    color: #555;
                }

                .sortType{
                    font-weight: 500;
                }

                img{
                    width: 15px;
                    cursor: pointer;
                }
                .rightMenu{
                    padding:20px;
                    background-color: white;
                    border-radius: 5px;
                    border: 0.5px solid lightgray;
                    position: absolute;
                    top: 30px;
                    right: 0px;
                    display: flex;
                    flex-direction: column;
                    gap: 20px;
                    color: #555;

                    span{
                        cursor: pointer;
                    }
                }
            }

    }
    .cards{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    }
}